import React,{useState, useEffect} from 'react';
import './Payment.css';
import CheckoutProduct from './CheckoutProduct';
import {useStateValue} from './StateProvider';
import {Link,  useNavigate} from "react-router-dom";
import { CardElement, useStripe, useElements } from '@stripe/react-stripe-js';
import CurrencyFormat from "react-currency-format";
import {getBasketTotal} from './reducer';
import axios from 'axios';


function Payment() {
    const [{basket}, dispatch] = useStateValue();

    const navigate = useNavigate();

    const stripe = useStripe();
    const elements= useElements();

    const [succeded, setSucceded]= useState(false);
    const [processing, setProcessing] = useState("");
    

    const [error, setError] = useState(null);
    const [disabled, setDisabled] = useState(true);

    const [clientSecret, setClientSecret] = useState();

    useEffect(()=>{
// generate the special stripe secret which allows us to charge a customer

     const getClientSecret = async ()=>{
         const response = await axios({
          method: 'post',
          //stripe expects the total in a currencies subunits
          url: `/Payment/create?total=${getBasketTotal(basket)* 100}`
         });
         setClientSecret(response.data.clintSecret)
     }
     getClientSecret();
    },[basket])

    const handleSubmit = async (e) =>{
     e.preventDefault();
     setProcessing(true);

     const payload = await stripe.confirmCardPayment(clientSecret,{
         payment_method:{
          card: elements.getElementlement(CardElement)
         }
     }).then(({paymentIntent})=>{
      //paymentIntent = payment confirmation
       
      setSucceded(true);
      setError(null);
      setProcessing(false)

      navigate.replace('/orders')
     });
    }

    const handleChange = e=>{
    //  listen for changes in the  CardElememnt
    //and display any errors as the customer types their card details
    setDisabled(e.empty);
    setError(e.error ? e.error.message: "");
    }
  return (
    <div className='payment'>
        <div className='payment-container'>
         <h1>
          Checkout (<Link to="/Checkout">{basket.length} items</Link>)
         </h1>

         {/* {payment section_ delivery address} */}
         <div className='payment-section'>
            <div className='payment-title'>
                <h3>Delivery Address</h3>
            </div>
           <div className='payment-address'>
            <p>{}</p>
            <p>123 React Lane</p>
            <p>Los angeles, CA</p>
           </div>

         </div>
         {/* {payment section_ review items} */}
         <div className='payment-section'>
           <div className='payment-title'>
            <h3>Review items and delivery</h3>
           </div>
           <div className='payment-items'>
           {basket.map(item =>(
          <CheckoutProduct
          id={item.id}
          title={item.title}
          image={item.image}
          price={item.price}
          rating={item.rating}
          />
        ))}
           </div>

         </div>
          {/* {payment section_ payment methid} */}
         <div className='payment-section'>
          <div className='payment-title'>
            <h3>Payment method</h3>
          </div>
           <div className='payment-details'>
            {/* stripe magic will go */}
            <form onSubmit={handleSubmit}>
              <CardElement onChange={handleChange}/>
              <div className='payment-priceContainer'>
              <CurrencyFormat
               renderText={(value) => (
                <>
               <h3>Order Total: {value}</h3>
               </>
              )}
               decimalScale={2}
               value={getBasketTotal(basket)}
                displayType={"text"}
                 thousandSeparator={true}
                 prefix={"$"}
                  />
                  <button disabled={processing || disabled || succeded}>
                    <span>{processing ? <p>Processing</p>: "Buy Now"}</span>
                  </button>
              </div>
              {/* {Error} */}
              {error && <div>{error}</div>}
            </form>
           </div>
        </div>   
        </div>
        </div>
  )
}

export default Payment;