import React from 'react';
import './Header.css';
import SearchIcon from '@mui/icons-material/Search';
import ShoppingBasketIcon from '@mui/icons-material/ShoppingBasket';
import { Link } from 'react-router-dom';
import { useStateValue } from './StateProvider';
function Header() {

  // const [{basket}, dispatch] = useStateValue();
  const [{ basket }, dispatch] = useStateValue();
  return (
    <div className='header'>
      {/* this link tag is used for linking logoimg to the home page */}
      <Link to="/">
      <img className="header-logo" src="http://pngimg.com/uploads/amazon/amazon_PNG11.png"/>
      </Link>
    
    <div className='header-search'>
        <input className='header-searchInput' type="text"/>
    
    <SearchIcon className="header-SearchIcon" >
    </SearchIcon>
    </div>
    <div className='header-nav'>
      <Link to='/Login'>
       <div className='header-option'>
          <span className="header__optionLineOne">Helloo user</span>

          <span className="header__optionLineTwo">Sign In</span>
       </div>
       </Link>

       <div className='header-option'>
        
       <span className="header__optionLineOne">Returns</span>

       <span className="header__optionLineTwo">Orders</span>
       </div>

       <div className='header-option'>
       <span className="header__optionLineOne">Your</span>

       <span className="header__optionLineTwo">Prime</span>
       </div>

        <Link to="/Checkout">
        <div className='header-optionBasket'>
         <ShoppingBasketIcon />
         <span className='header-optionLineTwo header-basketCount'>{basket?.length}</span>
        </div>
        </Link>
        


    </div>
    </div>
  )
}

export default Header;