const reducer1 = (state, action) =>{
    console.log(action);
    switch(action.type){
        case 'ADD_TO_BASKET':
            return{
                ...state,
                basket: [...state.basket, action.item],
            };
        case 'REMOVE_FROM_BASKET':
            
                //this code remove the item comparing the id its cause all sameid items remove from the basket on one click
                // ...state,
                // basket: state.basket.filter(item=> item.id !== action.id)

                const index = state.basket.findIndex((basketItem)=>basketItem.id === action.id);

                let newBasket = [...state.basket];

                if (index >= 0){
                  newBasket.splice(index, 1);
                }else{
                    console.warn(`cant resolve product (id:${action.id})as its not in basket`
                    )
                }
                return{
                    ...StaticRange,
                    basket: newBasket
                };
            
            default:
                return state;
    }
};
export default reducer1;